import React from 'react'

export class Profile extends React.Component{
    render(){
        var containerStyle={
            position:'relative',
            border:'5px',
            borderStyle:'outset',
            display: 'inline-block',
            flexDirection: 'column',
            float:'left',
            left:'28%',
            backgroundColor:'#FFFFFF',
            justifyContent: 'center',
            margin:'10px 10px 5px 10px',
            alignContent: 'space-around'
           
        };
        var imageStyle={
            margin:'20px 5px 50px 20px',
            width: '300px',            
            overflow: "hidden",    
            float:'left',
            
        };
        var articleStyle={
        borderRight:"1px solid #000",
        borderLeft:"1px solid #000",
        wordWrap: "break-word",
        width: "400px",
        margin:'10px 10px 5px 20px',
        padding: '0px 5px 0px 5px',
        float:'left',
        color: '#222',
        fontFamily: 'Open Sans sansSerif',
        fontSize: '15px',
        fontWeight: '400',
        lineHeight: '24px',
        letterSpacing: '0.09em',
        
        
        };
        var logoStyle={
            
            position:'absolute',
            bottom:'5px',
            left:'5px',
            margin:'20px 10px 5px 10px'
        };
        return (
            <div className="Profile-container" style={containerStyle}>
                
                <img src={this.props.url} style={imageStyle} alt='profile-img' border='0'></img>
                
                <div style={articleStyle}><font size="5">Hello there! My name is <i>{this.props.name}</i></font>
                 <hr></hr>
                 <font size="4">This is my online portfolio, I have been learning javascript and HTML so I wanted to test my web-develompent skills creating this web-portfolio.
                    In this page I'm going to introduce myself, if you want to read about my professional career you can click the  
                    <a href='https://laur2000.github.io/web-portfolio/CV.html'> <b>CV</b> </a>link .
                    <hr></hr>
                    As you can see below I'm a Software Engineer student from University of Seville, I had started programming since I was 15 because of my curiosity about
                    how videogames works. I have finished my first videogame prototype at the age of 16, and since then I continued learning about software development.
                    But that's enough talking about computers, I'm not a full-time nerd, I also like sports, like calisthenics. I love travelling and I am a very social person,
                    I have no problem explaining my ideas to other people as well as listening to theirs. So thats all about me, if you are interested you can contact me at
                    my personal mail: bogdan.lorenzo11@gmail.com</font>
                </div>
                <div style={logoStyle}> <img src={this.props.foot} display='inline-block' width='25%' alt='logo-img' border='0'></img></div>
                
            </div>
            
        );
    }
}

export default Profile