import React from 'react'
import Linker from './Linker'
export class Footermain extends React.Component {
    render(){
        var headerStyle={
                position: 'absolute',
                bottom:'0px',
                height: '55px',
                width: '100%',
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
              
                
                
                
               
                
        };
        return (
            <div className="Footer" style={headerStyle}>
                <Linker height={headerStyle.height} name="ig-button"url="https://www.instagram.com/lorenzobg/" image="https://i.ibb.co/rv1QMpL/instagram.png"></Linker>
                <Linker height={headerStyle.height} name="fb-button"url="https://www.facebook.com/lorenzo.george.125" image="https://i.ibb.co/0q63CmC/facebook.png"></Linker>
                <Linker height={headerStyle.height} name="in-button" url="https://www.linkedin.com/in/laurentiu-bogdan-b1a784173/" image="https://i.ibb.co/pdFSzjj/linkedin.png"></Linker>
            </div>
        );
    }
}

export default Footermain;