import React from 'react'

export class Linker extends React.Component {
    
    render(){
        var imageStyle={
            position:'relative',
            width:'auto',
            maxHeight:'100%',
            margin:'auto'
            
            
        };
        var refStyle={
            position:'relative',
            width:'auto',
            maxHeight:'100%',
            margin:'2px 10px 2px 10px'
            
            
        };
        return (            
                <a href={this.props.url} style={refStyle}  >
                    <img src={this.props.image}  alt={this.props.name} style={imageStyle} border="0" />
                </a>
            
        );
    }
}

export default Linker;